@font-face {
  font-family: "Lato";
  src: local("LatoBold"), url("../../assets/fonts/LatoBold.woff2") format("woff2"), url("../../assets/fonts/LatoBold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Lato";
  src: local("LatoSemibold"), url("../../assets/fonts/LatoSemibold.woff2") format("woff2"), url("../../assets/fonts/LatoSemibold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Lato";
  src: local("LatoMedium"), url("../../assets/fonts/LatoMedium.woff2") format("woff2"), url("../../assets/fonts/LatoMedium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Lato";
  src: local("LatoRegular"), url("../../assets/fonts/LatoRegular.woff2") format("woff2"), url("../../assets/fonts/LatoRegular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Inter";
  src: local("InterRegular"), url("../../assets/fonts/InterRegular.woff2") format("woff2"), url("../../assets/fonts/InterRegular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Korataki";
  src: local("KoratakiRegular"), url("../../assets/fonts/KoratakiRegular.woff2") format("woff2"), url("../../assets/fonts/KoratakiRegular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
:root {
  --font-base: "Lato", "Inter", "Korataki", sans-serif;
  --font-lato: "Lato", sans-serif;
  --font-roboto: "Roboto", sans-serif;
  --font-korataki: "Korataki", sans-serif;
  --font-inter: "Inter", sans-serif;
  --font-open-sans: "Open Sans", sans-serif;
  --color-primary: #E45302;
  --color-secondary: #808080;
  --color-black: #231815;
  --color-black-dark: #0D0B26;
  --color-black-footer: #0C0C0C;
  --color-white: #FFFFFF;
  --color-border-gray: #000000;
  --color-text-white: var(--color-white);
  --color-text-black: var(--color-black);
  --border-radius-base: 5rem;
  --border-radius-article: 1.25rem;
  --border-radius-full: 50%;
  --space-box: 2.5rem;
  --space-section: 3.75rem;
  --space-content: 16.25rem;
  --space-banner-link: 4.375rem;
  --space-nav-link: 6.25rem;
  --space-price: 1rem;
  --space-payment-logo: 1.8rem;
  --space-xs: .3rem;
  --space-sm: .75rem;
  --space-md: 1.25rem;
  --space-lg: 2rem;
  --space-xl: 2.375rem;
  --space-xxl: 4.375rem;
  --space-burger-line: .213rem;
  --space-button: .4rem;
  --space-button-md: .8rem;
  --space-button-x: 1.3rem;
  --space-button-y: 1.3rem;
  --space-input: .7rem;
  --space-input-x: 1.35rem;
  --space-input-y: 1.35rem;
  --font-size-base: 1rem;
  --font-size-md: 1.125rem;
  --font-size-lg: 1.375rem;
  --font-size-xl: 2.75rem;
  --font-size-h1: var(--font-size-xl);
  --font-size-h2: var(--font-size-lg);
  --font-size-h3: var(--font-size-md);
  --font-size-h4: var(--font-size-base);
  --font-weight-base: 400;
  --font-weight-md: 600;
  --font-weight-lg: 700;
  --line-height-base: 1rem;
  --line-height-md: 1.575rem;
  --line-height-lg: 1.375rem;
  --line-height-xl: 1.875rem;
  --icon-size-base: 1.313rem;
  --burger-button-width: 1.44rem;
  --header-height: 3.813rem;
  --logo-width: 8.813rem;
  --footer-logo-width: 14.938rem;
  --burger-line-height: 0.1rem;
  --article-width: 20rem;
  --article-overlay-bg-color: rgba(245, 245, 245, 0.7);
  --article-overlay-height: 5.5rem;
  --article-grid-row-gap: 3.125rem;
  --article-grid-column-gap: 2.5rem;
  --article-grid-template-columns: repeat(4, 1fr);
  --footer-padding-y: 3.75rem;
}

@media (max-width: 1600px) {
  :root {
    --space-content: 11rem;
    --font-size-xl: 2.5rem;
    --article-grid-row-gap: 1.875rem;
    --article-grid-column-gap: 1.813rem;
    --footer-padding-y: 3.125rem;
  }
}
@media (max-width: 1024px) {
  :root {
    --space-content: 2.75rem;
    --font-size-xl: 2rem;
    --space-section: 3.5rem;
  }
}
@media (max-width: 770px) {
  :root {
    --article-grid-column-gap: 2.5rem;
    --article-grid-row-gap: 3.125rem;
    --article-grid-template-columns: repeat(2, 1fr);
    --footer-padding-y: 2.5rem;
  }
}
/* Mobile Styles */
@media (max-width: 390px) {
  :root {
    --space-box: 0.625rem;
    --article-grid-row-gap: 1.875rem;
    --article-grid-column-gap: 0.625rem;
    --article-overlay-height: 3.5rem;
    --font-size-md: var(--font-size-base);
    --font-size-lg: var(--font-size-base);
    --font-size-xl: 1.5rem;
  }
}
* {
  margin: 0;
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
}

body {
  font-family: var(--font-base);
  font-weight: var(--font-weight-base);
}

.content, .header {
  margin: 0 auto;
  padding: 0 var(--space-content);
}

.banner {
  background-color: var(--color-black);
  padding-top: var(--space-sm);
  padding-bottom: var(--space-sm);
}
.banner .banner_container {
  padding: 0 var(--space-content);
  text-align: right;
  margin: 0 auto;
}
.banner .banner_container .banner__link {
  color: var(--color-white);
  text-decoration: none;
  margin-right: var(--space-banner-link);
}
.banner .banner_container .banner__link:last-of-type {
  margin-right: 0;
}
@media screen and (max-width: 770px) {
  .banner {
    display: none;
  }
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: var(--space-sm);
  padding-bottom: var(--space-sm);
  position: sticky;
  top: 0;
  z-index: 100;
  background-color: white;
}
.header .header__logo {
  width: var(--logo-width);
}
.header .header__nav {
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-left: var(--space-xs);
  width: 30rem;
  max-width: 50%;
}
.header .header__nav .header__nav-item {
  margin-right: var(--space-xs);
}
.header .header__nav .header__nav-item:last-of-type {
  margin-right: 0;
}
@media screen and (max-width: 770px) {
  .header .header__nav {
    display: none;
  }
}
.header .header_container {
  display: flex;
  align-items: center;
  margin-left: var(--space-xs);
  height: var(--header-height);
}
.header .header_container .header__search-container {
  display: flex;
  padding: var(--space-input) var(--space-input-x);
  align-items: center;
  border-radius: var(--border-radius-base);
  border: solid 1px var(--color-border-gray);
}
.header .header_container .header__search-container .header__search-input {
  outline: none;
  border: none;
  font-size: var(--font-size-base);
}
.header .header_container .header__search-container .header__search-icon {
  cursor: pointer;
  width: var(--icon-size-base);
  height: var(--icon-size-base);
}
.header .header_container .header__search-container .header__search-icon-white {
  display: none;
}
@media screen and (max-width: 1600px) {
  .header .header_container .header__search-container {
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 2.438rem;
    height: 2.438rem;
    border-radius: var(--border-radius-full);
    border: none;
    background-color: var(--color-black);
    cursor: pointer;
  }
  .header .header_container .header__search-container .header__search-icon {
    display: none;
  }
  .header .header_container .header__search-container .header__search-icon-white {
    display: inline-block;
  }
  .header .header_container .header__search-container .header__search-input {
    display: none;
  }
}
.header .header_container .header__cart {
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--color-text-white);
  border: none;
  border-radius: var(--border-radius-base);
  cursor: pointer;
  padding: var(--space-button) var(--space-button-x);
  background-color: var(--color-black);
  position: relative;
  margin-left: var(--space-box);
}
.header .header_container .header__cart .header__cart-text {
  margin-left: var(--space-xs);
  margin-right: var(--space-xs);
  font-size: var(--font-size-base);
}
@media screen and (max-width: 1050px) {
  .header .header_container .header__cart .header__cart-text {
    display: none;
  }
}
.header .header_container .header__cart .header__cart-count {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 3px;
  left: 2.25rem;
  width: 18px;
  height: 18px;
  border-radius: var(--border-radius-full);
  background-color: var(--color-white);
  color: var(--color-text-black);
}
@media screen and (max-width: 1050px) {
  .header .header_container .header__cart {
    margin-left: var(--space-xs);
  }
}
@media screen and (max-width: 770px) {
  .header .header_container {
    display: none;
  }
}

.footer {
  font-family: var(--font-roboto);
  background-color: var(--color-black-footer);
  margin-top: var(--space-section);
  padding: var(--footer-padding-y) var(--space-content);
  color: var(--color-white);
}
.footer .footer__base {
  display: grid;
  grid-template-columns: repeat(10, 1fr);
}
.footer .footer__base .footer__logo-box {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  grid-column: 1/5;
}
.footer .footer__base .footer__logo {
  width: var(--footer-logo-width);
}
.footer .footer__base .footer__privacy-text {
  font-family: var(--font-roboto);
  color: var(--color-secondary);
  margin-top: var(--space-xs);
  max-width: 24rem;
}
.footer .footer__base .footer__privacy-text a {
  color: var(--color-secondary);
}
@media screen and (max-width: 550px) {
  .footer .footer__base .footer__privacy-text {
    display: none;
  }
}
.footer .footer__base .footer__privacy-text-mobile {
  font-family: var(--font-roboto);
  display: none;
  color: var(--color-secondary);
  margin-top: var(--space-xs);
  max-width: 24rem;
}
.footer .footer__base .footer__privacy-text-mobile a {
  color: var(--color-secondary);
}
@media screen and (max-width: 550px) {
  .footer .footer__base .footer__privacy-text-mobile {
    display: block;
    margin-top: var(--space-lg);
  }
}
.footer .footer__base .footer__info {
  display: flex;
  justify-content: space-between;
  grid-column: 5/9;
}
.footer .footer__base .footer__info .footer__info-contact-title, .footer .footer__base .footer__info .footer__info-services-title, .footer .footer__base .footer__info .footer__info-address-title {
  margin-bottom: var(--space-md);
}
.footer .footer__base .footer__info .footer__info-first-column .footer__info-address .footer__info-address-subtitle {
  margin-bottom: var(--space-md);
}
.footer .footer__base .footer__info .footer__info-first-column .footer__info-contact .footer__info-contact-subtitle {
  margin-bottom: var(--space-md);
}
.footer .footer__base .footer__info .footer__info-first-column .footer__info-contact .footer__info-contact-subtitle:last-of-type {
  margin-bottom: 0;
}
.footer .footer__base .footer__info .footer__info-second-column {
  margin-left: var(--space-xs);
}
.footer .footer__base .footer__info .footer__info-second-column .footer__info-services-subtitle {
  margin-bottom: var(--space-md);
}
.footer .footer__base .footer__info .footer__info-second-column .footer__info-services-subtitle:last-of-type {
  margin-bottom: 0;
}
.footer .footer__base .footer__end {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: space-between;
  margin-left: var(--space-xs);
  grid-column: 9/-1;
}
.footer .footer__base .footer__end .footer__links {
  text-align: end;
  margin-bottom: var(--space-xs);
}
.footer .footer__base .footer__end .footer__link {
  display: block;
  color: var(--color-secondary);
  text-decoration: none;
}
.footer .footer__base .footer__end .footer__link:last-child {
  margin-top: var(--space-md);
}
.footer .footer__base .footer__end .footer__payments {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  max-width: 14rem;
}
@media screen and (max-width: 770px) {
  .footer .footer__base {
    display: none;
  }
}
@media screen and (max-width: 550px) {
  .footer .footer__base {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
  }
  .footer .footer__base .footer__logo-box, .footer .footer__base .footer__info, .footer .footer__base .footer__end {
    grid-column: auto;
  }
  .footer .footer__base .footer__info {
    flex-direction: column;
    justify-content: unset;
    margin-top: var(--space-lg);
  }
  .footer .footer__base .footer__info .footer__info-services-title {
    margin-top: var(--space-lg);
  }
  .footer .footer__base .footer__end {
    align-items: unset;
    justify-content: unset;
    margin-left: 0;
    margin-top: var(--space-box);
  }
  .footer .footer__base .footer__end .footer__links {
    text-align: left;
    margin-bottom: var(--space-xxl);
    margin-top: var(--space-lg);
  }
}
.footer .footer__tablet {
  display: none;
}
@media screen and (max-width: 770px) {
  .footer .footer__tablet {
    display: block;
  }
  .footer .footer__tablet .footer__logo {
    margin-bottom: var(--space-lg);
  }
  .footer .footer__tablet .footer__info {
    display: flex;
    justify-content: space-between;
  }
  .footer .footer__tablet .footer__info .footer__info-contact-title, .footer .footer__tablet .footer__info .footer__info-services-title, .footer .footer__tablet .footer__info .footer__info-address-title {
    margin-bottom: var(--space-md);
  }
  .footer .footer__tablet .footer__info .footer__info-services-subtitle {
    margin-bottom: var(--space-md);
  }
  .footer .footer__tablet .footer__info .footer__info-services-subtitle:last-of-type {
    margin-bottom: 0;
  }
  .footer .footer__tablet .footer__info .footer__info-services {
    margin-left: var(--space-xs);
  }
  .footer .footer__tablet .footer__info .footer__info-address-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  .footer .footer__tablet .footer__info .footer__info-address-container .footer__links {
    display: flex;
    flex-direction: column;
  }
  .footer .footer__tablet .footer__info .footer__info-address-container .footer__links .footer__link {
    color: var(--color-secondary);
    text-decoration: none;
  }
  .footer .footer__tablet .footer__info .footer__info-address-container .footer__links .footer__link:last-child {
    margin-top: var(--space-md);
  }
  .footer .footer__tablet .footer__info .footer__info-contact {
    margin-left: var(--space-xs);
  }
  .footer .footer__tablet .footer__info .footer__info-contact .footer__info-contact-subtitle {
    margin-bottom: var(--space-md);
  }
  .footer .footer__tablet .footer__info .footer__info-contact .footer__info-contact-subtitle:last-of-type {
    margin-bottom: 0;
  }
  .footer .footer__tablet .footer__end {
    display: flex;
    justify-content: space-between;
    margin-top: var(--space-box);
  }
  .footer .footer__tablet .footer__end .footer__privacy-text {
    color: var(--color-secondary);
    margin-top: var(--space-xs);
    max-width: 24rem;
  }
  .footer .footer__tablet .footer__end .footer__privacy-text a {
    color: var(--color-secondary);
  }
  .footer .footer__tablet .footer__end .footer__payments {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    max-width: 14rem;
  }
  .footer .footer__tablet .footer__end .footer__privacy-text {
    margin-left: var(--space-xs);
  }
}
@media screen and (max-width: 550px) {
  .footer .footer__tablet {
    display: none;
  }
}

.header__burger {
  display: none;
}
.header__burger .header__burger-toggle {
  display: none;
}
.header__burger .header__burger-toggle:checked ~ .header__burger-nav {
  height: calc(100vh - var(--header-height));
}
.header__burger .header__burger-toggle:checked ~ .header__burger-button .header__burger-line:nth-child(1) {
  transform: rotate(45deg) translateY(9px);
}
.header__burger .header__burger-toggle:checked ~ .header__burger-button .header__burger-line:nth-child(2) {
  opacity: 0;
}
.header__burger .header__burger-toggle:checked ~ .header__burger-button .header__burger-line:nth-child(3) {
  transform: rotate(-45deg) translateY(-7.5px) translateX(-2px);
}
.header__burger .header__burger-button {
  width: var(--burger-button-width);
  display: block;
  cursor: pointer;
}
.header__burger .header__burger-button .header__burger-line {
  height: var(--burger-line-height);
  margin-bottom: var(--space-burger-line);
  background-color: var(--color-black-dark);
  transition: all 0.3s ease;
}
.header__burger .header__burger-nav {
  text-align: center;
  background-color: var(--color-black);
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  height: 0;
  overflow: hidden;
  transition: height 0.3s ease;
  z-index: 100;
}
.header__burger .header__burger-nav .header__burger-nav-list {
  list-style: none;
}
.header__burger .header__burger-nav .header__burger-nav-list .header__burger-nav-item {
  margin-top: var(--space-box);
}
.header__burger .header__burger-nav .header__burger-nav-list .header__burger-nav-item .header__burger-nav-link {
  color: var(--color-white);
  text-decoration: none;
  font-size: var(--font-size-base);
}
@media screen and (max-width: 770px) {
  .header__burger {
    display: block;
  }
}

.search-result {
  margin-top: var(--space-section);
}
.search-result__title {
  font-size: var(--font-size-h1);
  font-family: var(--font-korataki);
}
.search-result .search-result__container {
  margin-top: var(--space-box);
  display: grid;
  grid-template-columns: var(--article-grid-template-columns);
  row-gap: var(--article-grid-row-gap);
  column-gap: var(--article-grid-column-gap);
}

.article .article__container {
  position: relative;
  overflow: hidden;
  border-radius: var(--border-radius-article);
}
.article .article__container .article__image {
  width: 100%;
}
.article .article__container .article__badge {
  position: absolute;
  top: 0.625rem;
  left: 0.625rem;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--color-text-white);
  border: none;
  border-radius: var(--border-radius-base);
  cursor: pointer;
  padding: var(--space-button) var(--space-button-x);
  background-color: var(--color-black);
}
.article .article__container .article__btn-details {
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--color-text-white);
  border: none;
  border-radius: var(--border-radius-base);
  cursor: pointer;
  padding: var(--space-button) var(--space-button-x);
  background-color: var(--color-black);
  padding: var(--space-button-md) 0;
  background-color: var(--color-primary);
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 1.25rem;
  width: 90%;
  z-index: 1;
}
@media (max-width: 390px) {
  .article .article__container .article__btn-details {
    bottom: 0.9rem;
    padding: 0.5rem;
  }
}
.article .article__container .article__overlay {
  width: 100%;
  height: var(--article-overlay-height);
  background-color: var(--article-overlay-bg-color);
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 0;
}
.article .article__title {
  font-size: var(--font-size-h3);
  margin-top: var(--space-md);
  margin-bottom: var(--font-size-md);
}
.article .article__price {
  color: var(--color-black);
  font-size: var(--font-size-lg);
  font-weight: 700;
}
.article .article__sale {
  color: var(--color-primary);
  margin-right: var(--space-price);
  font-size: var(--font-size-lg);
  font-weight: 700;
}
.article .article__no-sale {
  text-decoration: line-through;
  color: var(--color-secondary);
  font-size: var(--font-size-lg);
  font-weight: 700;
}